"use client";
// React and Next
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
import moment from "moment";
// Redux
import { useDispatch } from "react-redux";
import {
  checkoutPriceWithoutTax,
  checkoutPriceWithTax,
  couponDiscountValue,
  enableFailedOrder,
  failedOrderCheckoutObj,
} from "@/redux/slices/checkout";

// API
import { FailedOrderDetailApi } from "@/api/clientsideaction/order";

// Utils
import { failedOrderRetryPaymentHours, imageUrl } from "@/utilities/url";
import { PriceTaxCalculate } from "@/utilities/PriceCalculate";

// Components
import Sidebar from "@/Components/Myprofile/sidebar";
import Breadcrumb from "@/Components/Breadcrumbs";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";

export default function FailedOrderDetail({ params }) {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const router = useRouter();
  const [orderdata, setOrderData] = useState([]);
  const [buttonLoader, setButtonLoader] = useState(false);
  const [enableRetry, setEnableRetry] = useState(false);
    const orderBreadCrumbEdit = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.orderHistory"),
      url: "/account-settings/order-history",
    },
    {
      text: t("Breadcrumb.orderDetail"),
      url: "javascript:void(0)",
    },
  ];

  useEffect(() => {
    FailedOrderDetailApi(params, setOrderData, setEnableRetry);
  }, []);

  const handleTax = (data) => {
    let totalPrice = 0;

    orderdata?.forEach((item) => {
      if (item?.taxType == 2) {
        let count = Number(item?.productPrice) * (Number(item?.taxValue) / 100);
        totalPrice += count * item?.quantity;
      } else if (item?.taxType == 1) {
        totalPrice += Number(item?.taxValue) * item?.quantity;
      }
    });
    return totalPrice;
  };

  const handleTotalPrice = () => {
    let totalPrice = 0;

    orderdata?.forEach((item) => {
      let price = Number(item?.basePrice);
      totalPrice += item?.quantity * price;
    });
    return totalPrice;
  };

  const handleDisCountPrice = () => {
    let totalPrice = 0;
    orderdata?.forEach((item) => {
      totalPrice += Number(item?.discountAmount) * item?.quantity;
    });
    return totalPrice;
  };

  const handleCouponPrice = () => {
    let totalPrice = 0;
    orderdata?.forEach((item) => {
      totalPrice += Number(item?.couponDiscountAmount);
    });
    return totalPrice;
  };

  const handleTotalQyt = () => {
    let totalPrice = 0;
    orderdata?.forEach((item) => {
      totalPrice += item?.quantity;
    });
    return totalPrice;
  };

  const handleRetryPayment = () => {
    setButtonLoader(true);
    const tax = handleTax();
    const totalPrice = handleTotalPrice() - handleDisCountPrice();
    const couponDiscount = handleCouponPrice();

    const data = {
      orderPrefixId: orderdata?.at(0)?.orderPrefixId,
      isMobile: 0,
    };

    dispatch(failedOrderCheckoutObj(data));
    dispatch(checkoutPriceWithoutTax(totalPrice));
    dispatch(checkoutPriceWithTax(tax));
    dispatch(couponDiscountValue(couponDiscount));

    dispatch(enableFailedOrder(true));
    router.push("/payment");
  };
  const priceGroup = orderdata.find((order) => order?.priceGroupDetailId !== 0);

  return (
    <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
      <Breadcrumb breadCrumb={orderBreadCrumbEdit} />

      <div className="flex gap-8 mx-auto mt-4 mb-7">
        <Sidebar />
        <div className="w-full">
          <div className="flex items-end justify-between pt-0 pb-6 mb-6 border-b border-slate-200">
            <div className="flex flex-col items-start">
              <h3 className="mb-1 text-lg font-black text-black-300">
                {t("orderhistory.OrdDtl")}
              </h3>
              <p className="mb-3 text-base font-normal text-black-300">
                {t("orderhistory.OrdOn")}{" "}
                {moment(orderdata?.at(0)?.createdDate)?.format(
                  "DD MMM, YYYY, hh:mm A"
                )}{" "}
                | {t("orderhistory.Order")} #{orderdata?.at(0)?.orderPrefixId}{" "}
                {priceGroup?.priceGroupDetailId &&
                  `| ${t("orderhistory.PrsnlzdOrd")} `}
              </p>
              {enableRetry && (
                <p className="flex items-center gap-[5px] bg-[#fff5f5] p-2 border border-[#e29292] rounded w-auto font-medium text-black-300 text-sm">
                  <img src="/img/info-alert.svg" alt="info-alert" />{" "}
                  {t("orderhistory.retryPay")} {failedOrderRetryPaymentHours}{" "}
                  {t("orderhistory.timeLimRetry")}
                </p>
              )}
            </div>
          </div>
          <div className="flex flex-col items-start justify-between mb-4 space-y-4 lg:flex-row lg:space-x-20 lg:space-y-0">
            <div className="w-full">
              <div className="flex flex-col space-y-4">
                <h3 className="text-base font-semibold text-boldblack">
                  {" "}
                  {t("orderhistory.ShippingAddress")}
                </h3>
                <div className="flex flex-col space-y-1">
                  <p className="text-sm font-normal text-black-300">
                    {orderdata?.at(0)?.customerFirstName &&
                      orderdata?.at(0)?.customerFirstName}{" "}
                    {orderdata?.at(0)?.shippingAddress1 && ","}
                  </p>

                  <p className="text-sm font-normal text-black-300">
                    {orderdata?.at(0)?.shippingAddress1 &&
                      orderdata?.at(0)?.shippingAddress1}{" "}
                    {orderdata?.at(0)?.shippingAddress2 && ","}
                  </p>

                  <p className="text-sm font-normal text-black-300">
                    {orderdata?.at(0)?.shippingAddress2 &&
                      orderdata?.at(0)?.shippingAddress2}{" "}
                    {orderdata?.at(0)?.shippingCity && ","}
                  </p>

                  <p className="text-sm font-normal text-black-300">
                    {orderdata?.at(0)?.shippingCity &&
                      orderdata?.at(0)?.shippingCity}{" "}
                    {orderdata?.at(0)?.shippingPostcode && ","}
                  </p>

                  <p className="text-sm font-normal text-black-300">
                    {orderdata?.at(0)?.shippingPostcode &&
                      orderdata?.shippingPostcode}
                  </p>
                </div>
              </div>
              <div>
                {orderdata?.map((order, index) => (
                  <div key={index} className="py-4">
                    <div className="px-4 py-4 border border-[#D1DADF] rounded-[5px]">
                      <div className="flex items-start justify-between">
                        <div className="flex items-start space-x-4 rtl:space-x-[unset]">
                          <div className="rtl:ml-4 w-[100px] min-w-[100px] h-[100px]">
                            <img
                              src={
                                imageUrl +
                                "?path=" +
                                order?.containerName +
                                "&name=" +
                                order?.image +
                                "&width=400&height=200"
                              }
                              alt={order?.productName}
                              onError={({ currentTarget }) => {
                                currentTarget.onerror = null;
                                currentTarget.src = "/img/no-image.png";
                                currentTarget.alt = "no-image";
                              }}
                              className="w-auto h-full max-w-full m-auto"
                            />
                          </div>
                          <div className="flex flex-col items-start gap-4">
                            <h6 className="text-base font-normal text-black-300">
                              {order?.productNameTrans
                                ? order?.productNameTrans
                                : order?.productName}
                            </h6>
                            <p className="mb-0 text-lg font-black text-black-300">
                              <CustomCurrencyFormatter
                                price={order?.productPrice}
                              />
                            </p>
                            {Number(order?.discountedAmount) !== 0 && (
                              <div className="flex items-center space-x-2 rtl:space-x-[unset]">
                                <p className="text-sm font-normal rtl:ml-2 text-black-300">
                                  {t("productdetails.Mrp")}{" "}
                                  <del>
                                    <CustomCurrencyFormatter
                                      price={PriceTaxCalculate(
                                        order?.basePrice,
                                        order?.taxType,
                                        order?.taxValue
                                      )}
                                    />
                                  </del>
                                </p>
                                <span className="text-sm font-normal text-activegreen">
                                  {Math.round(
                                    ((PriceTaxCalculate(
                                      order?.basePrice,
                                      order?.taxType,
                                      order?.taxValue
                                    ) -
                                      PriceTaxCalculate(
                                        order?.discountedAmount,
                                        order?.taxType,
                                        order?.taxValue
                                      )) /
                                      PriceTaxCalculate(
                                        order?.basePrice,
                                        order?.taxType,
                                        order?.taxValue
                                      )) *
                                      100
                                  )}
                                  {t("productdetails.OFF")}
                                </span>
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {orderdata?.paymentMethod && (
              <div className="flex flex-col space-y-4">
                <h3 className="text-base font-normal text-black-300">
                  {" "}
                  {t("orderhistory.PaymentMethod")}
                </h3>
                <div className="flex flex-col space-y-1">
                  <p className="text-sm font-normal text-black-300">
                    {orderdata?.paymentMethod}
                  </p>
                </div>
              </div>
            )}

            <div className="flex flex-col w-full lg:min-w-[350px] lg:max-w-[350px]">
              <div className="flex flex-col space-y-4">
                <h3 className="text-base font-semibold text-black-300">
                  {t("orderhistory.OrdSum")}
                </h3>
                <div className="flex flex-col space-y-2">
                  <div className="flex items-center justify-between">
                    <h5 className="text-base font-semibold text-black-300">
                      {t("orderhistory.Quantity")}:
                    </h5>
                    <p className="font-normal text-black-300">
                      {handleTotalQyt()}
                    </p>
                  </div>
                  <div className="flex items-center justify-between">
                    <h5 className="text-base font-semibold text-black-300">
                      {t("orderhistory.BasePrice")}:
                    </h5>
                    <p className="font-normal text-black-300">
                      <CustomCurrencyFormatter price={handleTotalPrice()} />
                    </p>
                  </div>
                  <div className="flex items-center justify-between">
                    <h5 className="text-base font-semibold text-black-300">
                      {t("orderhistory.Tax")}:
                    </h5>
                    <p className="font-normal text-black-300">
                      <CustomCurrencyFormatter price={handleTax()} />
                    </p>
                  </div>
                  <div className="flex items-center justify-between">
                    <h5 className="text-base font-semibold text-black-300">
                      {t("orderhistory.Discount")}:
                    </h5>
                    <p className="font-normal text-black-300">
                      <CustomCurrencyFormatter price={handleDisCountPrice()} />
                    </p>
                  </div>
                  <div className="flex items-center justify-between">
                    <h5 className="text-base font-semibold text-black-300">
                      {t("orderhistory.CpnDisc")}:
                    </h5>
                    <p className="font-normal text-black-300">
                      <CustomCurrencyFormatter price={handleCouponPrice()} />
                    </p>
                  </div>
                  <div className="flex items-center justify-between">
                    <h5 className="text-base font-semibold text-black-300">
                      {t("orderhistory.GndTot")}:
                    </h5>
                    <p className="font-normal text-black-300">
                      <CustomCurrencyFormatter
                        price={
                          handleTotalPrice() -
                          handleDisCountPrice() -
                          handleCouponPrice() +
                          handleTax()
                        }
                      />
                    </p>
                  </div>
                  <div className="relative mt-6 tooltip4">
                    <button
                      disabled={enableRetry}
                      onClick={(e) => handleRetryPayment()}
                      className="bg-black hover:bg-zinc-800 px-8 rounded w-full h-[49px] font-black text-white text-base btn"
                    >
                      {buttonLoader == true ? (
                        <svg
                          aria-hidden="true"
                          role="status"
                          className="inline w-5 h-5 text-white me-3 animate-spin"
                          viewBox="0 0 100 101"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                            fill="#E5E7EB"
                          />
                          <path
                            d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                            fill="currentColor"
                          />
                        </svg>
                      ) : (
                        t("orderhistory.retry")
                      )}
                    </button>
                    {enableRetry && (
                      <div className="flex items-center justify-center">
                        <span className="whitespace-normal shadow-md max-w-64 tooltip-text">
                          {t("orderhistory.retryPay")}
                        </span>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
